.hidden {
  display: none;
}

.breed-select {
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  font-size: 16px;
}

.loader {
  text-align: center;
  display: none;
}

.loader:before {
  content: "";
  width: 20px;
  height: 20px;
  border: 3px solid #ccc;
  border-top-color: #333;
  border-radius: 50%;
  animation: spin 1s ease-in-out infinite;
  display: inline-block;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #c00;
  background-color: #fdd;
  border: 1px solid #c00;
  border-radius: 4px;
  padding: 16px;
  font-size: 16px;
  display: none;
}

.cat-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
}

.breed-name {
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
}

.breed-description {
  margin-top: 5px;
  font-size: 16px;
}

.breed-temperament {
  color: #00f;
  margin-top: 5px;
  font-size: 18px;
  font-style: italic;
}

